import visualize from "react-dom-visualizer"; // should be included before React
// imp/ort React from "react";
import { render } from "react-dom";


// render(<App />, document.getElementById("app"));

const React = require("react");
const ReactDom = require("react-dom/client");

const h1 = React.createElement("h1", {}, "Namaste Guys"); // react element
const h2 = React.createElement("h2", { id: "lamb", key: "rom" }, [
  React.createElement("h3", {}, "Namaste EveryOne!"),
]);
const container = React.createElement("div", { div: "container" }, [h1, h2]);
const root = ReactDom.createRoot(document.getElementById("root"));

root.render(container);
visualize({
  selector: "#viz",
  width: 1000,
  height: 800,
  displayLabels: false,
  treeConfig: {
    width: 1000,
    height: 600,
  },
});
